.App {
  width: 1000px;
  height: 400px;
  background-color: #3a9285;
  display: -ms-flexbox;
  display: flex;
  border-radius: 25px 25px 25px 25px;
  border: 1px solid black;
}

.Drums {
  margin: 40px;
  width: 660px;
  height: 135px;
  background-color: #3a9285;
  position: relative;
  border-radius: 15px;
  display: inline-block;

}

.soundCont {
  display: block;
  width: 80%;
}

.stateBar {
  width: 215px;
  height: 50px;
  border: 1px solid black;
  display: inline-block;
  background: #fff;
  line-height: 50px;
  border-radius: 25px 25px 25px 25px;
  font-weight: 900;
}

.volume {
  display: inline-block;
  margin-top: 30px;
}


.Controls {
  width: 25%;
  height: 300px;
  display: inline-block;
  vertical-align: center;
  margin-top: 50px;
  -ms-flex-line-pack: center;
      align-content: center;
  -ms-flex-pack: center;
      justify-content: center;
  text-align: center;
}

/*volume bar from https://css-tricks.com/styling-cross-browser-compatible-range-inputs-css/ */
input[type=range] {
  -webkit-appearance: none;
  margin: 18px 0;
  width: 210px;
}
input[type=range]:focus {
  outline: none;
}
input[type=range]::-webkit-slider-runnable-track {
  width: 100%;
  height: 8.4px;
  cursor: pointer;
  animate: 0.2s;
  -webkit-box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
          box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
  background: #c2d6d1;
  border-radius: 1.3px;
  border: 0.2px solid #010101;
}
input[type=range]::-webkit-slider-thumb {
  -webkit-box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
          box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
  border: 1px solid #000000;
  height: 36px;
  width: 16px;
  border-radius: 3px;
  background: #dc4a1f;
  cursor: pointer;
  -webkit-appearance: none;
  margin-top: -14px;
}
input[type=range]:focus::-webkit-slider-runnable-track {
  background: #c2d6d1;
}
input[type=range]::-moz-range-track {
  width: 100%;
  height: 8.4px;
  cursor: pointer;
  animate: 0.2s;
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
  background: #a3c2ba;
  border-radius: 1.3px;
  border: 0.2px solid #010101;
}
input[type=range]::-moz-range-thumb {
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
  border: 1px solid #000000;
  height: 36px;
  width: 16px;
  border-radius: 3px;
  background: #ffffff;
  cursor: pointer;
}
input[type=range]::-ms-track {
  width: 100%;
  height: 8.4px;
  cursor: pointer;
  animate: 0.2s;
  background: transparent;
  border-color: transparent;
  border-width: 16px 0;
  color: transparent;
}
input[type=range]::-ms-fill-lower {
  background: #2a6495;
  border: 0.2px solid #010101;
  border-radius: 2.6px;
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
}
input[type=range]::-ms-fill-upper {
  background: #3071a9;
  border: 0.2px solid #010101;
  border-radius: 2.6px;
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
}
input[type=range]::-ms-thumb {
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
  border: 1px solid #000000;
  height: 36px;
  width: 16px;
  border-radius: 3px;
  background: #ffffff;
  cursor: pointer;
}
input[type=range]:focus::-ms-fill-lower {
  background: #3071a9;
}
input[type=range]:focus::-ms-fill-upper {
  background: #367ebd;
}


@media (max-width: 1000px){
  .App {
    width: 768px;
    height: 300px;
  }
  input[type=range] {
  width: 170px;
}
.stateBar {
  width: 100%;
}

.Controls {
  width: 25%;
  height: 200px;
}

}

@media (max-width: 767px) {
  .App {
    width: 420px;
    display: block;
    height: auto;
  }
  .soundCont {
    display: block;
    width: 100%;
  }
  .stateBar {
    width: 100%;
  }
  input[type=range] {
    -webkit-appearance: none;
    margin: 18px 0;
    width: 350px;
  }
  .Controls {
    width: 100%;
    height: 150px;
    margin: 25px auto;
    display: grid;
  }


}

@media (max-width: 375px) {
  .App {
    width: 320px;
    display: block;
    height: auto;
  }
  .soundCont {
    display: block;
    width: 100%;
  }
  .stateBar {
    width: 100%;
  }
  input[type=range] {
    -webkit-appearance: none;
    margin: 18px 0;
    width: 300px;
  }
  .Controls {
    width: 100%;
    height: 150px;
    margin: 25px auto;
    display: grid;
  }


}


